"use client"

import { useState, useEffect, DragEvent } from "react";
import { Plus, MoreHorizontal, X, DollarSign, Calendar, User, Mail, Trash2, Edit2, Paperclip, MessageSquare, AlertCircle, CheckCircle, Clock, Smartphone, AlertTriangle, Filter, Search } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/navigation";

// --- Types ---
type PrintStage = "New Order" | "File Check" | "Printing" | "Cutting" | "Finishing" | "Ready for Pickup" | "Shipped";

interface Order {
    id: string;
    customer: string;
    phone: string;
    total: number;
    status: PrintStage;
    deadline: string;
    source: string;
    priority: string;
    fileStatus: string;
    specs: {
        productName: string;
        size: string;
        material: string;
        quantity: number;
        department: string;
    };
}

export default function AdminOrdersPage() {
    const router = useRouter();
    const [orders, setOrders] = useState<Order[]>([]);
    const [isLoaded, setIsLoaded] = useState(false);
    const [highlightedId, setHighlightedId] = useState<string | null>(null);
    const [isSidebarOpen, setIsSidebarOpen] = useState(true);

    // Load from LocalStorage
    useEffect(() => {
        const saved = localStorage.getItem("web2print_orders");
        if (saved) {
            try {
                setOrders(JSON.parse(saved));
            } catch (e) {
                console.error("Failed to parse orders", e);
            }
        }
        setIsLoaded(true);
    }, []);

    // Save to LocalStorage
    useEffect(() => {
        if (isLoaded) {
            localStorage.setItem("web2print_orders", JSON.stringify(orders));
        }
    }, [orders, isLoaded]);

    const updateStatus = (id: string, newStatus: PrintStage) => {
        setOrders(prev => prev.map(o => o.id === id ? { ...o, status: newStatus } : o));
    };

    // --- Drag and Drop Handlers ---
    const onDragStart = (e: DragEvent, id: string) => {
        e.dataTransfer.setData("orderId", id);
    };

    const onDragOver = (e: DragEvent) => {
        e.preventDefault();
    };

    const onDrop = (e: DragEvent, newStatus: PrintStage) => {
        const id = e.dataTransfer.getData("orderId");
        if (id) {
            updateStatus(id, newStatus);
        }
    };

    const handleHighlight = (id: string) => {
        setHighlightedId(id);
        const element = document.getElementById(`card-${id}`);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth', block: 'center' });
            setTimeout(() => setHighlightedId(null), 3000);
        }
    };

    const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);

    if (!isLoaded) return <div className="p-8">Loading...</div>;

    const columns: { title: string, status: PrintStage, dotColor: string }[] = [
        { title: "New Order", status: "New Order", dotColor: "bg-gray-400" },
        { title: "File Check", status: "File Check", dotColor: "bg-yellow-400" },
        { title: "Printing", status: "Printing", dotColor: "bg-blue-400" },
        { title: "Cutting", status: "Cutting", dotColor: "bg-indigo-400" },
        { title: "Finishing", status: "Finishing", dotColor: "bg-purple-400" },
        { title: "Ready", status: "Ready for Pickup", dotColor: "bg-green-400" },
        { title: "Shipped", status: "Shipped", dotColor: "bg-gray-400" },
    ];

    // Sidebar Data
    const overdueOrders = orders.filter(o => new Date(o.deadline) < new Date() && o.status !== 'Shipped');
    const issueOrders = orders.filter(o => o.fileStatus === 'issue');
    const urgentOrders = orders.filter(o => o.priority === 'Urgent' && o.status !== 'Shipped');

    return (
        <div className="h-full flex flex-col bg-white">
            {/* Top Bar */}
            <div className="flex justify-between items-center px-6 py-4 border-b">
                <div className="flex items-center gap-4">
                    <h2 className="text-xl font-bold text-gray-900">Production Board</h2>
                    <div className="h-6 w-px bg-gray-200"></div>
                    <button
                        onClick={toggleSidebar}
                        className={`p-1.5 rounded-md transition-colors text-xs font-medium flex items-center gap-2 ${isSidebarOpen ? 'bg-gray-100 text-gray-900' : 'text-gray-500 hover:bg-gray-50'}`}
                    >
                        <AlertCircle className="h-4 w-4" /> Reminders
                    </button>
                    {/* Filter Mock */}
                    <button className="flex items-center gap-2 text-xs font-medium text-gray-500 hover:text-gray-900">
                        <Filter className="h-3.5 w-3.5" /> Filter <span className="bg-gray-100 text-gray-900 px-1.5 py-0.5 rounded-full text-[10px]">3</span>
                    </button>
                </div>

                <Link href="/orders/new">
                    <button className="flex items-center gap-2 bg-blue-600 text-white px-3 py-1.5 rounded-md hover:bg-blue-700 transition-colors font-medium text-sm shadow-sm ring-offset-1 focus:ring-2">
                        <Plus className="h-4 w-4" /> New Order
                    </button>
                </Link>
            </div>

            <div className="flex-1 flex overflow-hidden">

                {/* Kanban Board Area */}
                <div className="flex-1 overflow-x-auto overflow-y-hidden bg-gray-50/50">
                    <div className="flex h-full p-6 gap-6 min-w-max">
                        {columns.map(col => (
                            <div
                                key={col.status}
                                onDragOver={onDragOver}
                                onDrop={(e) => onDrop(e, col.status)}
                                className="flex flex-col w-[280px] min-w-[280px]"
                            >
                                {/* Column Header (Clean Pill Style) */}
                                <div className="flex items-center justify-between mb-3 px-1">
                                    <div className="flex items-center gap-2">
                                        <div className="flex items-center gap-2 bg-gray-100/80 px-2 py-1 rounded-md text-xs font-semibold text-gray-700">
                                            <div className={`w-2 h-2 rounded-full ${col.dotColor}`} />
                                            {col.title}
                                            <span className="text-gray-400 ml-1">{orders.filter(o => o.status === col.status).length}</span>
                                        </div>
                                    </div>
                                    <button className="text-gray-400 hover:text-gray-600">
                                        <Plus className="h-4 w-4" />
                                    </button>
                                </div>

                                {/* Drop Area */}
                                <div className="flex-1 overflow-y-auto pr-2 pb-10 space-y-3">
                                    {orders.filter(o => o.status === col.status).map(order => (
                                        <OrderCard
                                            key={order.id}
                                            order={order}
                                            onDragStart={onDragStart}
                                            isHighlighted={highlightedId === order.id}
                                        />
                                    ))}

                                    {/* Ghost 'New' Button */}
                                    <button className="flex items-center gap-2 text-gray-400 hover:text-gray-600 px-2 py-1.5 text-sm transition-colors w-full text-left rounded hover:bg-gray-100/50">
                                        <Plus className="h-4 w-4" /> New
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Right Sidebar (Reminder) */}
                <div
                    className={`
                        w-[320px] bg-white border-l z-20 flex flex-col
                        transform transition-all duration-300 ease-in-out
                        ${isSidebarOpen ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0 w-0 hidden'}
                    `}
                >
                    <div className="p-4 border-b flex items-center justify-between">
                        <h3 className="font-bold text-sm text-gray-900">Action Required</h3>
                        <span className="text-xs bg-red-100 text-red-600 px-2 py-0.5 rounded-full font-bold">
                            {issueOrders.length + overdueOrders.length + urgentOrders.length}
                        </span>
                    </div>

                    <div className="flex-1 overflow-y-auto p-4 space-y-6">

                        {/* Issues */}
                        {issueOrders.length > 0 && (
                            <div className="space-y-2">
                                <h4 className="text-[11px] font-bold text-gray-500 uppercase tracking-wider flex items-center gap-2">
                                    <AlertTriangle className="h-3 w-3" /> File Issues
                                </h4>
                                {issueOrders.map(o => (
                                    <SidebarItem key={o.id} order={o} onClick={() => handleHighlight(o.id)} type="issue" />
                                ))}
                            </div>
                        )}

                        {/* Overdue */}
                        {overdueOrders.length > 0 && (
                            <div className="space-y-2">
                                <h4 className="text-[11px] font-bold text-gray-500 uppercase tracking-wider flex items-center gap-2">
                                    <Clock className="h-3 w-3" /> Overdue
                                </h4>
                                {overdueOrders.map(o => (
                                    <SidebarItem key={o.id} order={o} onClick={() => handleHighlight(o.id)} type="overdue" />
                                ))}
                            </div>
                        )}

                        {/* Urgent */}
                        {urgentOrders.length > 0 && (
                            <div className="space-y-2">
                                <h4 className="text-[11px] font-bold text-gray-500 uppercase tracking-wider flex items-center gap-2">
                                    <AlertCircle className="h-3 w-3" /> Urgent
                                </h4>
                                {urgentOrders.map(o => (
                                    <SidebarItem key={o.id} order={o} onClick={() => handleHighlight(o.id)} type="urgent" />
                                ))}
                            </div>
                        )}

                        {issueOrders.length === 0 && overdueOrders.length === 0 && urgentOrders.length === 0 && (
                            <div className="flex flex-col items-center justify-center h-40 text-gray-400 text-sm">
                                <CheckCircle className="h-8 w-8 mb-2 opacity-20" />
                                <p>All caught up!</p>
                            </div>
                        )}
                    </div>
                </div>

            </div>
        </div>
    );
}

function SidebarItem({ order, onClick, type }: { order: Order, onClick: () => void, type: 'issue' | 'overdue' | 'urgent' }) {
    const borderClass = type === 'issue' ? 'border-red-200 bg-red-50' : type === 'overdue' ? 'border-orange-200 bg-orange-50' : 'border-purple-200 bg-purple-50';

    return (
        <div onClick={onClick} className={`p-3 border rounded-lg cursor-pointer hover:shadow-sm transition-all text-sm ${borderClass}`}>
            <div className="flex justify-between items-start mb-1">
                <span className="font-mono font-bold text-gray-600 text-xs">#{order.id}</span>
                <span className="text-[10px] text-gray-500">{order.status}</span>
            </div>
            <p className="font-medium text-gray-900 truncate">{order.customer}</p>
        </div>
    )
}

function OrderCard({ order, onDragStart, isHighlighted }: any) {
    const isOverdue = new Date(order.deadline) < new Date();

    return (
        <div
            id={`card-${order.id}`}
            draggable
            onDragStart={(e) => onDragStart(e, order.id)}
            className={`
                bg-white p-4 rounded-lg border shadow-sm cursor-grab active:cursor-grabbing hover:shadow-md transition-all group relative
                ${isHighlighted ? 'ring-2 ring-blue-500 shadow-lg scale-[1.02] z-10' : 'border-gray-200'}
            `}
        >
            <div className="space-y-3">
                {/* Tags */}
                <div className="flex flex-wrap gap-1.5">
                    {order.specs?.department && (
                        <span className="text-[10px] font-medium px-2 py-0.5 rounded-full bg-gray-100 text-gray-600">
                            {order.specs.department}
                        </span>
                    )}
                    {order.priority === 'Urgent' && (
                        <span className="text-[10px] font-medium px-2 py-0.5 rounded-full bg-red-100 text-red-700 flex items-center gap-1">
                            <AlertCircle className="h-3 w-3" /> Urgent
                        </span>
                    )}
                </div>

                {/* Content */}
                <div>
                    <h4 className="font-medium text-sm text-gray-900 leading-snug">
                        {order.specs?.quantity}x {order.specs?.productName}
                    </h4>
                    <p className="text-xs text-gray-500 mt-1 line-clamp-2">
                        {order.specs?.size}, {order.specs?.material}
                    </p>
                </div>

                {/* Footer Info */}
                <div className="flex items-center justify-between pt-2 border-t border-gray-50">
                    <div className="flex items-center gap-2">
                        {/* File Status Indicator */}
                        {order.fileStatus === 'ok' ? (
                            <div className="flex items-center gap-1 text-[10px] font-medium text-green-600 bg-green-50 px-1.5 py-0.5 rounded">
                                <CheckCircle className="h-3 w-3" /> Ready
                            </div>
                        ) : order.fileStatus === 'issue' ? (
                            <div className="flex items-center gap-1 text-[10px] font-medium text-red-600 bg-red-50 px-1.5 py-0.5 rounded">
                                <AlertTriangle className="h-3 w-3" /> Fix File
                            </div>
                        ) : (
                            <div className="flex items-center gap-1 text-[10px] font-medium text-gray-500 bg-gray-100 px-1.5 py-0.5 rounded">
                                <Clock className="h-3 w-3" /> Checking
                            </div>
                        )}
                    </div>

                    <div className="flex items-center gap-2 opacity-60 group-hover:opacity-100 transition-opacity">
                        {isOverdue && <Clock className="h-3.5 w-3.5 text-red-500" />}
                        {order.phone && (
                            <a
                                href={`https://wa.me/${order.phone.replace(/[^0-9]/g, '')}`}
                                target="_blank"
                                className="hover:text-green-600 hover:scale-110 transition-all"
                                onClick={(e) => e.stopPropagation()}
                            >
                                <Smartphone className="h-3.5 w-3.5" />
                            </a>
                        )}
                    </div>
                </div>

                {/* Progress (Mock) for 'Printing' state */}
                {order.status === 'Printing' && (
                    <div className="w-full bg-gray-100 h-1 rounded-full overflow-hidden mt-1">
                        <div className="bg-blue-500 h-full w-[60%] animate-pulse" />
                    </div>
                )}
            </div>
        </div>
    );
}
